import React, { startTransition } from "react";
import styled from "styled-components";
import { motion } from "framer-motion";
import { useSelector } from "react-redux";
import { useNavigate } from 'react-router-dom';
import { smallImage } from "../util";
import playStation from "../img/playstation.svg";
import steam from "../img/steam.svg";
import xbox from "../img/xbox.svg";
import nintendo from "../img/nintendo.svg";
import apple from "../img/apple.svg";
import gamepad from "../img/gamepad.svg";
import strarEmpty from "../img/star-empty.png";
import startFull from  "../img/star-full.png"; 
const GameDetail=({pathId})=>{
  const history=useNavigate();
  const exitDetailHandler=(e)=>{
    const element=e.target;
    if(element.classList.contains("shadow")){
      document.body.overflow="auto";
      history("/")
    }
  }
  //Star logic
  const getStars=()=>{
    const stars=[];
    const rating=Math.floor(game.rating);
      for(let i = 1 ; i<=5 ; i++ ){
        if(i <= rating){
          stars.push(<img alt="star" key={i} src={startFull}></img>)
        }else{
          stars.push(<img alt="star" key={i} src={strarEmpty}></img>)
        }
      }
      return stars
  }
  const getPlatform=(platform)=>{
    switch(platform){
      case "PlayStation 4":
        return playStation;
      case "PlayStation 5":
        return playStation;
      case "Xbox Series S/X":
        return xbox;
      case "Xbox S":
        return xbox;
      case "Xbox One":
        return xbox;
      case "PC":
        return steam;
      case "Nintendo Switch":
        return nintendo;
      case "iOS":
        return apple;
      default:
        return gamepad;
    }

  }
  const {screen,game,isLoading}=useSelector(state=>state.detail)
  return(
    <>
    {!isLoading &&(
    <CardShadow className="shadow" onClick={exitDetailHandler}>
      <Detail layoutId={pathId}>
        <Stats>
          <div className="rating">
            <motion.h3 layoutId={`title ${pathId}`}>{game.name}</motion.h3>
            <p>Rating:{game.rating}</p>
            {getStars()}
          </div>
          <Info>
            <h3>Platfroms</h3>
            <Platfroms>
              {game.platforms?.map(data=>(
              <img 
              src={getPlatform(data.platform.name)} 
              key={data.platform.id}
              alt="platform name" ></img>
              ))}
            </Platfroms>
          </Info>
        </Stats>
        <Media>
          <motion.img layoutId={`image ${pathId}`} src={smallImage(game.background_image,1920)} alt="pic" />
        </Media>
        <Decription>
          <p>{game.description_raw}</p>
        </Decription>
        <div className="gallarey">
          {screen.results?.map(screen=>(<img src={
            smallImage(screen.image,1920)} key={screen.id} alt="pic2"></img>))}
        </div>
      </Detail>
    </CardShadow>
    )}
    </>
  )
}

const CardShadow=styled(motion.div)`
 width:100%;
 min-height: 100vh;
 overflow-y: scroll;
 background:rgba(0,0,0,0.5);
 position: fixed;
 top:0;
 left:0;
 z-index:10;
 &::-webkit-scrollbar{
  width:0.5rem
 }
 &::-webkit-scrollbar-thump{
  background: #ff7676
 }  
 &::-webkit-scrollbar-track{
  background:white
 }
`
const Detail=styled(motion.div)`
  width:80%;
  border-radius: 1rem ;
  padding:2rem 5rem;
  background: white;
  position: absolute;
  left:10%;
  color:black;
  img{
    width:100%;
  }
`

const Stats=styled(motion.div)`
  display:flex;
  align-items: center;
  justify-content: space-between;
  img{
     width:2rem;
     height: 2rem;
     display: inline;
  }
`

const Info=styled(motion.div)`
  text-align: center;
`

const Platfroms=styled(motion.div)`
  display:flex;
  justify-content: space-evenly;
  img{
    margin-left:3rem
  }
`
const Media=styled(motion.div)`
  margin-top:5rem;
  img{
    width:100%;
    height:60vh;
    object-fit: cover;
  }
`

const Decription=styled(motion.div)`
  margin:5rem 0;
`

export default GameDetail;